@import '../../../Stylesheets/Variables.scss';

.sidebar {
    width: 2%;
    background-size: 100%;
    background-position: 0% 100%;
    border-right: 0.1rem solid $brand-color-light;
    border-bottom: 0.1rem solid $brand-color-light;
    background-color: white;
    box-shadow: 0rem gray;
    position: absolute;
    top: 3rem;
    left: 0;
    height: 94.5vh;
    transform: translateX(-1px);
    transition: 10ms ease-in-out;
    z-index: 9;
    overflow: hidden;
    cursor: pointer;
    padding-top: 0rem;

    &__expanded {
        width: 25%;
        margin-left: 12rem;
        height: 94.5vh;
        position: absolute;
        background-size: 100%;
        background-position: 0 0;
        transform: translateX(0);
        background-color: white;
        transition: 10ms ease-in-out;
        padding-top: 0rem;
        border-right: 0.1rem solid $brand-color-light;
    }
}

.transcription-factor-panel {
    width: 100%;
}

.fa-caret-right {
    color: white;
}

.open-button-component {
    margin-top: 4rem;
    margin-right: 0%;
    float: right;
}
