@import '../../../../Stylesheets/Variables.scss';

.filters-component {
    height: auto;
    width: 100%;
    border-bottom: 0.1rem solid $brand-color-light;
    background-color: white;
    font-size: medium;
    font-family: 'Work Sans';

    &__header {
        padding: 1rem 0rem 0rem 1rem;
    }

    &__filters {
        padding: 0rem 0rem 1rem 0rem ;
    }
}

.search-component {
    width: fit-content;
    height: 2.2rem;
    border: 0.1rem solid #ced4da;
    border-radius: 0.2rem;
    font-size: small;
    
    &__input {
        width: 20.5rem;
        border: none;
        outline: none !important;
        background-color: white;
    }

    &__search-icon {
        background-color: white;
    }
}

.fa-search {
    color: $gray-light;
    padding: 0.5rem;
}

.fa-caret-left {
    color: white;
}

.close-sidebar-component {
    height: 2rem;
    border: 1px solid black;
    margin-top: 0.5rem;
}

button {
    background-color: $brand-color-dark;
    border: 1px solid $brand-color-dark;
    padding: 0.2rem 0.4rem;
}