@import '../../../Stylesheets/Variables.scss';

.header-content {
    padding-left: 2rem;
    padding-top: 1rem;
    font-size: medium;
}

.rate-component {
    font-size: small;
    font-family: $default-font-family;
}

.average-rating-component {
    background-color: $brand-color-alternate;
    border-radius: 0.3rem;
    height: fit-content;
    font-size: x-small;
    font-weight: bold;
    color: white;
    &__text {
        font-size: small;
    }
}

.fa-star {
    color: white;
}

.fa-question-circle {
    color:#4A90E2;
}