@import '../../../../Stylesheets/Variables.scss';

.report-container {
    font-family: 'Work Sans';
    width: 100%;
    height: 94.5vh;
    overflow-x: auto;
    
    &__header {
        font-size: medium;
        padding-bottom: 1rem;
        padding: 4% 0% 0% 10%;
    }

    &__list {
        :hover {
            background-color: $brand-color-lightest;
            color: $brand-color-dark;
        }
    }
}

.report-subsection-list {
    font-size: small;
    font-family: 'Work Sans';
    padding: 4% 0% 5% 10%;
    margin-left: 0rem;
    cursor: pointer;
    text-decoration: none;
    color: gray;
}


.link-component.activeSubsection .report-subsection-list{
    color: $brand-color-dark;
}


.activeSubsection {
    background-color: $brand-color-lightest;
    width: inherit;
    color: $brand-color-dark;
}