@import '../../../../Stylesheets/Variables.scss';

.tf-container {
    background-color: white;
    border-right: 1px $brand-color-light solid;
    font-family: 'Work Sans';
    width: 20rem;
    height: 94.5vh;
    
    &__header {
        font-size: medium;
        padding-bottom: 1rem;
        padding: 4% 0% 0% 10%;
    }

    &__list {
        :hover {
            background-color: $brand-color-lightest;
            color: $brand-color-dark;
        }
    }
}

.factor-list {
    font-size: small;
    font-family: 'Work Sans';
    padding: 4% 0% 5% 10%;
    margin-left: 0rem;
    cursor: pointer;
    text-decoration: none;
    color: black;
}

.link-component {
    text-decoration: none;
    color: black;
    font-size: medium;
}

.link-component.activeTF .factor-list{
    color: $brand-color-dark;
}

.activeTF {
    background-color: $brand-color-lightest;
    width: inherit;
}