@import '../../Stylesheets/Variables.scss';

.header-component {
    position: relative;
    height: 3rem;
    border-bottom: 1px $brand-color-light solid;
    padding-top: 1rem;
    z-index: 99;

    &__content {
        font-family: 'Work Sans', sans-serif;
        margin-left: 2rem;

        &__orange {
            color: $brand-color-alternate;
        }

        &__purple {
            color: $brand-color-dark;
        }
    }

}