@import 'src/Stylesheets/Variables.scss';

.main-panel {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: 2%;
    transition: 300ms ease-in-out;
    position: relative;
    z-index: 2;
    overflow-x: auto;
    
    &__expanded {
        width: 100%;
        transition: 300ms ease-in-out;
        position: relative;
        z-index: 2;
        margin-left: 25%;
        overflow-x: auto;
    }

    &__error-content {
        text-align: center;
        width: 25rem;
        margin-top: 20%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        font-size: x-large;
    }

    &__error-code {
        font-size: 5rem;
    }

    &__home-page-content {
        text-align: center;
        font-size: large;
        width: 25rem;
        margin-top: 20%;
        margin-left: 50%;
        margin-right: auto;
        margin-bottom: auto;
        font-family: 'Work Sans';
    }

    &__header {
        padding: 0.1rem;
        box-shadow: 0 4px 5px 0 $gray-lighter;
        font-family: 'Work Sans';
    }
}

.p-multiselect, 
.p-dropdown {
    width:  6rem;
    height: 2rem;
}

.p-multiselect-item, 
.p-multiselect-header, 
.p-dropdown-items {
    font-size: small;
}

.p-dropdown .p-dropdown-label,
.p-multiselect .p-multiselect-label,
.p-dropdown .p-dropdown-label.p-placeholder,
.p-multiselect .p-multiselect-label.p-placeholder {
    font-size: small;
}

.p-multiselect-filter-container {
    position: relative;
    flex: 1 1 auto;
    height: 2rem;
}

.p-multiselect-panel .p-multiselect-header .p-multiselect-filter-container .p-inputtext {
    padding-right: 1rem;
    height: 2rem;
    font-size: small;
}

.p-button {
    margin: 0rem 0rem 1rem 1rem !important;
    border-color: black;
    font-size: x-small;
}

.p-button.p-button-secondary.p-button-outlined {
    color: $gray-light !important;
}

.p-button-label {
    font-size: small;
}

.p-checkbox .p-checkbox-box,
.p-checkbox .p-checkbox-box.p-highlight {
    border-color: $brand-color-light;
    background: $brand-color-light;
}