// Standard Colors

$brand-color: #a190da;
$brand-color-lightest: #f6f6fb;
$brand-color-light: #ebe9fa;
$brand-color-highlight: #ececfd;
$brand-color-dark: #6a53b3;
$brand-color-dark-alternate: #795fcb;
$brand-color-alternate: #ec9023;

// Create grayscale
$gray-dark: #242424 !default;
$gray-text: #3a3a3a !default;
$gray-border: #e4e4e4 !default;
$gray: #6a6a6a !default;
$grey-shapes: #717171 !default;
$gray-light: #636c72 !default;
$gray-lighter: #eceeef !default;
$gray-lightest: #f7f7f9 !default;
$background-mask: rgba(0, 0, 0, 0.4);

// font-family
$default-font-family: 'Work Sans';
