@import '../../Stylesheets/Variables.scss';

.summary-panel-component{
    background-color: #241652;
    font-family: 'Work Sans';
    width: 12rem;
    height: 94.5vh;
}

.list-container {
    color: white;
    color: $brand-color-dark;
    font-size: large;
    &__data {
        padding: 0.5rem;
    }
    :hover {
        background-color: white;
        color: $brand-color-dark;
        border: 0.01rem #241652 solid;
    }
}

.link-component {
    color: white;
    text-decoration: none;
}

.active {
    color: $brand-color-dark;
    background-color: white;
    width: inherit;
    border-right: 0.1rem #241652 solid;
}